'use client';

import { useState, useEffect } from 'react';
import { useParams } from 'next/navigation';

import Wrapper from '@/assets/wrappers/midterm/Shop_58';

const FetchShopByCategory_58 = () => {
  const [shop_58, setShop_58] = useState([]);
  const params = useParams();
  const category = params.category;
  console.log('category', category);

  const fetchShopFromNode = async () => {
    try {
      const response = await fetch(
        `http://localhost:5000/api/shop_58/${category}`
      );
      const data = await response.json();
      console.log('shop_58 data', data);
      if (data.length !== 0) {
        setShop_58(data);
      }
    } catch (error) {
      console.log(err);
    }
  };

  useEffect(() => {
    fetchShopFromNode();
  }, []);

  return (
    <Wrapper>
      <div className='shop-page'>
        <div className='section-title'>
          <h4 className='text-center text-[16px]'> clw,213410458 </h4>
        </div>
        <div className='collection-page'>
          <h1 className='title'>{category}</h1>
          <div className='items'>
            {shop_58?.map((item) => {
              const { pid, pname, price, img_url } = item;
              return (
                <div className='collection-item'>
                  <img
                    className='image'
                    src='/images/midterm/womens/blue-tank.png'
                  />
                  <div className='collection-footer'>
                    <span className='name'>Blue Tanktop</span>
                    <span className='price'>25</span>
                  </div>
                  <button className='custom-button'>Add to Cart</button>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </Wrapper>
  );
};

export default FetchShopByCategory_58;
